@import "src/styles/global.scss"; @import "portal/global.scss";

















































































.demo-img {
    .img-item {
        display: inline-block;
        margin-right: 12px;
        cursor: pointer;
        transform-origin: center center;
        transition: transform 0.15s linear;

        &:hover {
            transform: scale(1.1);
        }

        img {
            border-radius: 4px;
            height: 100px;
            width: 100px;
            object-fit: cover;
            object-position: center;
            display: block;
        }
    }
}
